/* tslint:disable:jsx-no-lambda */
import * as moment from 'moment';
import * as React from 'react';
import './App.css';

class App extends React.Component {
    constructor(props: any) {
        super(props);
        const date = moment().format('YYYY-MM-DD HH:mm:ss');
        this.state = {
            inTime: date,
            num: 1,
            outTime: date,
            scanTime: date,
            showCode: false,
            showPage: false,
            time: date,
        };
        document.title = '学校临时出入校申请';
    }

    public handleShowPage = () => {
        // @ts-ignore
        const {showPage} = this.state;
        this.setState({
            showPage: !showPage,
        })
    };

    public handleChangeNav = (num: number) => {
        this.setState({num})
    };


    public handleTimeChange = (type: string, e: any) => {
        this.setState({[type]: e.target.value})
    };

    public handleShowCode = () => {
        this.setState({
            showCode: true,
        })
        document.title = '通行码';

    }

    public render() {
        // @ts-ignore
        const {showPage, num, time, outTime, inTime, showCode, scanTime} = this.state;
        return (
            <div className="App">
                {!showPage && !showCode && <ul>
									<li onClick={this.handleShowCode}>打开绿码</li>
									<li>
										填写请假时间
										<input type="text" value={time.toString()} onChange={(v) => this.handleTimeChange('time', v)} />
									</li>
									<li>
										填写入校时间
										<input type="text" value={inTime.toString()} onChange={(v) => this.handleTimeChange('inTime', v)} />
									</li>
									<li>
										填写出校时间
										<input type="text" value={outTime.toString()} onChange={(v) => this.handleTimeChange('outTime', v)} />
									</li>
									<li onClick={this.handleShowPage}>打开请假</li>
								</ul>}
                {showPage && <section className="page">
									<ul className={`nag`}>
										<li className={num === 1 ? `navSelected` : ''} onClick={() => this.handleChangeNav(1)}>
											申请表单
										</li>
										<li className={num === 2 ? `navSelected` : ''} onClick={() => this.handleChangeNav(2)}>办事流程</li>
										<li className={num === 3 ? `navSelected` : ''} onClick={() => this.handleChangeNav(3)}>处理记录</li>
									</ul>
                    {num === 3 && <ul className="page3">
											<li>
												<p>
													<div className="spot" />
													发起者
												</p>
												<p>
													<span className="name">
                                                      王靖淞</span>
													<span>仪器科学与光电工程学院 </span>
												</p>
												<span>操作意见：<span style={{color: '#5086ec'}}>提交</span></span>
												<p className="time" style={{color: '#6e6e6e'}}>{time}</p>
											</li>
											<li>
												<p>
													<div className="spot" />
													带班辅导员审核
												</p>

												<p>
													<span>范时秒</span>
													<span>仪器科学与光电工程学院 </span>
												</p>
												<span>操作意见：<span style={{color: '#5086ec'}}>同意</span></span>
												<p className="time" style={{color: '#6e6e6e'}}>{time}</p>
											</li>
											<li>
												<p>
													<div className="spot" />
													学生销假
												</p>
												<p>
													<span className="name">王靖淞</span>
													<span>仪器科学与光电工程学院 </span>
												</p>
												<span>操作意见：<span style={{color: '#8e8e8e'}}>等待中</span></span>
											</li>
										</ul>}
                    {num === 2 && <div className="page2"><img className="img" src="./1600515761941.jpg" alt="" /></div>}
                    {num === 1 && <div className="page1">
											<div className="title">
												<img src="./QQ20200919-202646@2x.png" alt="" />
												<div>
													学生临时出入校申请 Temporarily Leaving Entering
													Campus Application
												</div>

											</div>
											<ul>
												<li>
													<div className="type">
														姓名 name
													</div>
													<div className="content">
														王靖淞
													</div>
												</li>
												<li>
													<div className="type">
														学号 student id
													</div>
													<div className="content">
														ZB2017116
													</div>
												</li>
												<li>
													<div className="type">
														学院/书院 school/college

													</div>
													<div className="content">
														仪器科学与光电工程学院

													</div>
												</li>
												<li>
													<div className="type">
														联系方式 contact

													</div>
													<div className="content">
														17611281525

													</div>
												</li>
												<li>
													<div className="type">
														位置 location

													</div>
													<div className="content" style={{color: '#5086ec'}}>
														北京市/海淀区
													</div>
												</li>
												<li>
													<div className="type">
														申请类型 type
													</div>
													<div className="content">
														<div className="spotS">
															<div />
														</div>
														<span style={{marginRight: '10px'}}>
                                                            														离校 leaving he campus
                                                        </span>
														<div className="spotN" />
														入校 entering the campus
													</div>
												</li>
												<li>
													<div className="type">
														学生类别 Student Status
													</div>
													<div className="content">
														<div className="spotN" />
														<span style={{marginRight: '10px'}}>
														本科生 undergraduate
														</span>
														<div className="spotS">
															<div />
														</div>
														研究生 postgraduate
													</div>

												</li>
												<li>
													<div className="type">
														是否当日返校 Back to school that day

													</div>
													<div className="content">
														<div className="spotS">
															<div />
														</div>
														<span style={{marginRight: '10px'}}>
                                                          是 yes
                                                        </span>
														<div className="spotN" />
														否 no
													</div>
												</li>
												<li>
													<div className="type">
														本次申请是否已征得导师同意 Whether the application has been approved by your supervisor （若非当日返回，本次
														申请须征得导师同意，if it's not a same-day return, your application shall be approved by your
														supervisor)
													</div>
													<div className="content">
														<div className="spotN" />
														<span style={{marginRight: '10px'}}>
                                                          是 yes
                                                        </span>
														<div className="spotN" />
														否 no
													</div>
												</li>
												<li>
													<div className="type">
														入校事由 reasons of entering
													</div>
													<div className="content" />
												</li>
												<li>
													<div className="type">
														离校事由 reasons of leaving

													</div>
													<div className="content">
														其它（具体填写）others (write in detail)
													</div>
												</li>
												<li>
													<div className="type">
														其他离校事由 other reasons
													</div>
													<div className="content">
														探亲
													</div>
												</li>
												<li>
													<div className="type">
														上传附件 add attachments
													</div>
													<div className="content" />
												</li>
												<li>
													<div className="type">
														申请时间段 time slot
													</div>
													<div className="content contentTime">
														<span>
															申请出入校开始时间 start time
														</span>
														<span className="outTime">{inTime}</span>
														<span>
															申请出入校结束时间 end time
														</span>
														<span className="outTime">{outTime}</span>
													</div>
												</li>
												<li>
													<div className="type">
														离校去向 out-of-campus schedule
													</div>
													<div className="content">
														沙河到学院路，货拉拉
													</div>
												</li>
												<li>
													<div className="type">
														离校交通工具 traffic tools

													</div>
													<div className="content">
														地铁
													</div>
												</li>
												<li>
													<div className="type">
														销假地点 places where reporting back after leave of the
														campus
													</div>
													<div className="content" style={{color: '#5086ec'}}>
														获取地理位置
													</div>
												</li>
												<li>
													<div className="type">
														请上传销假附件 upload relevant attachments of reporting
														back
													</div>
													<div className="content" />
												</li>
												<li className="btnContainer">
													<button>预览表单</button>
													<button>催办</button>
												</li>
											</ul>
										</div>}
								</section>}
                {showCode && <div className="pageCode">
									<div className="bgc">
										<h1>
											<img src="./e850352ac65c103896bc6769b2119313b17e895f.jpeg" alt="" />
											北京航空航天大学</h1>
										<div>
											<div style={{marginBottom: '10px'}}>
												<div style={{display: 'inline-block', marginRight: '100px'}}>
													<span className="small">姓名</span>
													<span className="big">王靖淞</span>
												</div>
												<div style={{display: 'inline-block'}}>
													<span className="small">学工号</span>
													<span className="big">ZB2017116</span>
												</div>

											</div>
											<div>
												<div>
													<span className="small">单位</span>
													<span className="big">研究生新生</span>
												</div>
											</div>
										</div>

									</div>
									<div className="pageCodeContent code">
										<h4>通行码</h4>
										<span className='scanTime'>
                                            扫码时间:{scanTime}
                                        </span>
										<img src="./QQ20200919-213708@2x.png" alt="" />
									</div>
									<div className="pageCodeContent">
										<div className='local'>所在位置</div>
										<div className="local2">学院路校区-新东门（出）</div>
									</div>
									<div className="pageCodeContent pageCodeContentCity">
										<span>您 14 天内途径的城市：<span style={{fontSize: '12px', color: '#000'}}>北京市</span></span>
									</div>
									<div style={{margin: '30px 20px', backgroundColor: '#dadada', height: 1, position: 'relative'}}>
										<div className="tip">
											若对通行码有疑问，请联系 82317005
										</div>
									</div>
								</div>}
            </div>
        );
    }
}

export default App;



